import React from "react";

export class Line extends React.Component {
    entity = null;
    componentDidMount() {
        var entity = document.createElement('a-entity');
        entity.setAttribute('line', `start: ${this.props.x1} 0 ${this.props.y1}; end: ${this.props.x2} 0 ${this.props.y2}; color: green`);
        var container = document.querySelector(this.props.parent);
        this.entity = container.appendChild(entity);
    }

    componentWillUnmount() {
        this.entity.parentNode.removeChild(this.entity);
    }

    componentDidUpdate(prevProps) {
        if (prevProps.x1 !== this.props.x1 || prevProps.y1 !== this.props.y1 || prevProps.x2 !== this.props.x2 || prevProps.y2 !== this.props.y2) {
            this.entity.setAttribute('line', `start: ${this.props.x1} 0 ${this.props.y1}; end: ${this.props.x2} 0 ${this.props.y2}; color: green`);
        }
    }


    render() {
        return null;
    }
}