import React from "react";

export class Text extends React.Component {
    entity = null;

    componentDidMount() {
        var entity = document.createElement('a-text');
        entity.setAttribute('color', this.props.color ?? 'black');
        entity.setAttribute('position', { x: this.props.x ?? 0, y: this.props.z ?? .5, z: this.props.y ?? 0 });
        entity.setAttribute('rotation', { x: -90, y: this.props.rotation ?? 0, z: 0 })
        entity.setAttribute('value', this.props.text);

        var container = document.querySelector(this.props.parent);
        this.entity = container.insertBefore(entity, container.firstChild);

    }

    componentDidUpdate(prevProps) {
        if (prevProps.x !== this.props.x || prevProps.y !== this.props.y || prevProps.z !== this.props.z) {
            this.entity.setAttribute('position', { x: this.props.x ?? 0, y: this.props.z ?? .5, z: this.props.y ?? 0 });
        }

        if (prevProps.text !== this.props.text) {
            this.entity.setAttribute('value', this.props.text);
        }
    }

    componentWillUnmount() {
        this.entity.parentNode.removeChild(this.entity);
    }

    render() {
        return null;
    }
}