import React from 'react';
import ToggleButton from '@material-ui/lab/ToggleButton';
import Button from '@material-ui/core/Button'
import WeatherLayer from './weather-layer';
import WindLayer from './wind-layer';
import BordersLayer from './borders-layer';
import './index.css';
import { Terrain } from './terrain';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Grid from '@material-ui/core/Grid';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import DynamicLayer from './dynamic-layer';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import ListItemIcon from '@material-ui/core/ListItemIcon';

export default class WhiteboardMenu extends React.Component {


    constructor() {
        super();
        this.state = {
            loopIndex: 0,
            areZonesActive: true,
            areThreatsActive: true,
            areLabelsActive: true,
            isOrbitActive: true,
            isWeatherActive: false,
            isBordersActive: true,
            isWindActive: false,
            isTerrainActive: false,
            screenLocation: { left: 0, top: 0 },
            topLeft: false,
            bottomLeft: false,
            topRight: false,
            bottomRight: false,
            timer: null,
            markerPropertiesSelectedIndex: -1,
            isMarkerPropertiesDialogOpen: false,
            selectedMarkerType: "none",
            isMarkerTurnpoint: false,
            markerPropertiesTurnPointNumber: 0,
            markersHeader: "No Assets Visible",
            isAnchorVisible: false,
            markers: [],
            markersFound: 0,
            markersVisible: 0,

        }
    }

    addMarker(element, id) {
        var index = this.state.markers.findIndex((m) => m.element === element)

        var markers = [...this.state.markers];

        var anchorElement = document.querySelector("#anchor");
        var anchorPosition = anchorElement?.getAttribute("position");

        var markerElement = document.querySelector("#" + element);
        var markerPosition = markerElement?.getAttribute("position");

        var relativePosition = { x: markerPosition.x - anchorPosition.x, y: anchorPosition.y - markerPosition.y, z: 0 }


        var markersFound = this.state.markersFound;
        var markersVisible = this.state.markersVisible + 1;

        if (index !== -1) {
            var marker = { ...markers[index] }
            marker.position = markerPosition;
            marker.relativePosition = relativePosition;
            marker.isVisible = true;
            markers[index] = marker;
        }
        else {
            markersFound++;
            markers.push(
                {
                    element: element,
                    id: id,
                    name: `Marker ${id}`,
                    position: markerPosition,
                    relativePosition: relativePosition,
                    type: "none",
                    isMarkerTurnpoint: false,
                    turnpointIndex: 1,
                    isVisible: true
                }
            )
        }
        var markersHeader = `${markersVisible}/${markersFound} Assets Visible`
        this.setState({ markersFound: markersFound, markersVisible: markersVisible, markersHeader: markersHeader, markers: markers })
    }

    markerLost(element) {
        var index = this.state.markers.findIndex((m) => m.element === element)
        var markers = [...this.state.markers];
        if (index !== -1) {
            var marker = { ...markers[index] }
            marker.isVisible = false;
            markers[index] = marker;
        }
        var markersFound = this.state.markersFound;
        var markersVisible = this.state.markersVisible - 1;
        var markersHeader = `${markersVisible}/${markersFound} Assets Visible`
        this.setState({ markersVisible: markersVisible, markersHeader: markersHeader, markers: markers })
    }

    getUpdatedMarkers(markers) {
        var markersCopy = [...markers];

        var anchorElement = document.querySelector("#anchor");
        var anchorPosition = anchorElement?.getAttribute("position");

        if (anchorPosition) {
            for (var i = 0; i < markersCopy.length; i++) {

                var markerElement = document.querySelector("#" + markersCopy[i].element);
                var markerPosition = markerElement?.getAttribute("position");

                var relativePosition = { x: markerPosition.x - anchorPosition.x, y: anchorPosition.y - markerPosition.y, z: 0 }

                var marker = { ...markersCopy[i] }
                marker.position = markerPosition;
                marker.relativePosition = relativePosition;
                markersCopy[i] = marker;
            }
        }
        return markersCopy;
    }

    getUpdatedFlight(markers) {
        return markers.map((m) => m.relativePosition);
    }

    handleItemOnClick(marker, index) {
        var turnpointIndexes = this.state.markers.filter(m => m.isMarkerTurnpoint).map(m => m.turnpointIndex);
        var maxTurnpointIndex = Math.max(...turnpointIndexes) + 1;
        if (!isFinite(maxTurnpointIndex)) {
            maxTurnpointIndex = marker.turnpointIndex ?? 1;
        }

        if (maxTurnpointIndex > 8) {
            maxTurnpointIndex = 8;
        }

        this.setState(
            {
                isMarkerPropertiesDialogOpen: true,
                markerPropertiesSelectedIndex: index,
                selectedMarkerType: marker.type,
                isMarkerTurnpoint: marker.isMarkerTurnpoint,
                markerPropertiesTurnPointNumber: marker.isMarkerTurnpoint ? marker.turnpointIndex : maxTurnpointIndex,
            }
        );
    }

    closeMarkerPropertiesDialog() {
        this.setState({ isMarkerPropertiesDialogOpen: false, markerPropertiesSelectedIndex: -1 });
    }

    saveMarkerProperties() {
        var markersCopy = [...this.state.markers];

        var marker = { ...markersCopy[this.state.markerPropertiesSelectedIndex] }
        marker.type = this.state.selectedMarkerType;
        marker.isMarkerTurnpoint = this.state.isMarkerTurnpoint;
        marker.turnpointIndex = this.state.markerPropertiesTurnPointNumber;

        markersCopy[this.state.markerPropertiesSelectedIndex] = marker;

        if (this.state.isMarkerTurnpoint) {
            this.incrementTurnpointIndex(markersCopy, marker);
        }

        this.setMarkerNames(markersCopy);

        this.setState({ markers: markersCopy, isMarkerPropertiesDialogOpen: false, markerPropertiesSelectedIndex: -1 });
    }

    getMarkerName(marker) {
        var name = `Marker ${marker.id}`;
        if (marker.type === "none") {
            if (marker.isMarkerTurnpoint) {
                name = `Turn Point ${marker.turnpointIndex} (M${marker.id})`;
            }
            else {
                name = `Marker ${marker.id}`;
            }
        }
        else {
            var prefix = 'Asset'
            if (marker.type === "friendly") {
                prefix = "Friendly";
            }

            if (marker.type === "enemy") {
                prefix = "Enemy";
            }

            var tpText = "";
            if (marker.isMarkerTurnpoint) {
                tpText = ` (TP ${marker.turnpointIndex})`
            }
            name = `${prefix}${tpText} (M${marker.id})`;
        }

        return name
    }

    setMarkerNames(markers) {
        for (var i = 0; i < markers.length; i++) {
            var marker = { ...markers[i] }
            marker.name = this.getMarkerName(marker);
            markers[i] = marker;
        }
    }

    incrementTurnpointIndex(markers, marker) {
        if (marker.turnpointIndex === 8) {
            return markers;
        }

        var matchingIndexes = markers.filter(m => m.isMarkerTurnpoint && m.turnpointIndex === marker.turnpointIndex);
        if (matchingIndexes.length === 1) {
            return markers;
        }

        markers.forEach(m => {
            if (m === marker || !m.isMarkerTurnpoint || m.turnpointIndex < marker.turnpointIndex) {
                return m;
            }
            var incrementedIndex = m.turnpointIndex + 1;
            if (incrementedIndex > 8) {
                incrementedIndex = 8;
            }
            m.turnpointIndex = incrementedIndex;
            return m;
        })
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            var i = this.state.loopIndex + 1;
            if (i > 1000000) {
                i = 0;
            }
            if (this.state.isAnchorVisible) {
                var markers = this.state.markers;
                var updatedMarkers = this.getUpdatedMarkers(markers);
                if (updatedMarkers) {
                    markers = updatedMarkers;
                }
                this.setState({ loopIndex: i, markers: markers });
            }
            else {
                this.setState({ loopIndex: i });
            }

        }, 100);

        document.getElementById("anchor")?.addEventListener("markerFound", (e) => { console.log("Anchor Found!"); this.setState({ isAnchorVisible: true }) });
        document.getElementById("marker1")?.addEventListener("markerFound", (e) => { this.addMarker("marker1", "1") });
        document.getElementById("marker2")?.addEventListener("markerFound", (e) => { this.addMarker("marker2", "2") });
        document.getElementById("marker3")?.addEventListener("markerFound", (e) => { this.addMarker("marker3", "3") });
        document.getElementById("marker4")?.addEventListener("markerFound", (e) => { this.addMarker("marker4", "4") });
        document.getElementById("marker5")?.addEventListener("markerFound", (e) => { this.addMarker("marker5", "5") });
        document.getElementById("marker6")?.addEventListener("markerFound", (e) => { this.addMarker("marker6", "6") });
        document.getElementById("marker7")?.addEventListener("markerFound", (e) => { this.addMarker("marker7", "7") });
        document.getElementById("marker8")?.addEventListener("markerFound", (e) => { this.addMarker("marker8", "8") });

        document.getElementById("anchor")?.addEventListener("markerLost", (e) => { console.log("Anchor Lost!"); this.setState({ isAnchorVisible: false }) });
        document.getElementById("marker1")?.addEventListener("markerLost", (e) => { this.markerLost("marker1") });
        document.getElementById("marker2")?.addEventListener("markerLost", (e) => { this.markerLost("marker2") });
        document.getElementById("marker3")?.addEventListener("markerLost", (e) => { this.markerLost("marker3") });
        document.getElementById("marker4")?.addEventListener("markerLost", (e) => { this.markerLost("marker4") });
        document.getElementById("marker5")?.addEventListener("markerLost", (e) => { this.markerLost("marker5") });
        document.getElementById("marker6")?.addEventListener("markerLost", (e) => { this.markerLost("marker6") });
        document.getElementById("marker7")?.addEventListener("markerLost", (e) => { this.markerLost("marker7") });
        document.getElementById("marker8")?.addEventListener("markerLost", (e) => { this.markerLost("marker8") });
    }

    componentWillUnmount() {
        if (this.timer) {
            window.clearInterval(this.timer);
        }
    }

    resetRoute() {
        var markers = [...this.state.markers];

        for (var i = 0; i < markers.length; i++) {
            var marker = { ...markers[i] }
            marker.isMarkerTurnpoint = false;
            marker.turnpointIndex = 1;
            markers[i] = marker;
        }

        this.setMarkerNames(markers);
        this.setState({ markers: markers });
    }

    render() {
        return (
            <div>
                <Dialog open={this.state.isMarkerPropertiesDialogOpen} onClose={() => this.closeMarkerPropertiesDialog()} aria-labelledby="form-dialog-title">
                    <DialogTitle style={{ width: '250px' }} id="form-dialog-title">Marker Properties</DialogTitle>
                    <DialogContent>
                        <TextField select label={"Asset Type"} helperText={"Select an asset type"} value={this.state.selectedMarkerType} fullWidth onChange={(e) => this.setState({ selectedMarkerType: e.target.value })}>
                            <MenuItem value="none">None</MenuItem>
                            <MenuItem value="friendly">Friendly CVN</MenuItem>
                            <MenuItem value="enemy">Enemy Battle Group</MenuItem>
                        </TextField>
                        <FormControlLabel
                            control={<Switch
                                checked={this.state.isMarkerTurnpoint}
                                onChange={(e) => this.setState({ isMarkerTurnpoint: e.target.checked })}
                            />}
                            label="Is Turnpoint"
                        />
                        <TextField select label={"Turnpoint"} helperText={"Select a turn point number"} disabled={!this.state.isMarkerTurnpoint} value={`${this.state.markerPropertiesTurnPointNumber}`} fullWidth={false} onChange={(e) => this.setState({ markerPropertiesTurnPointNumber: parseInt(e.target.value) })}>
                            <MenuItem value="1">1</MenuItem>
                            <MenuItem value="2">2</MenuItem>
                            <MenuItem value="3">3</MenuItem>
                            <MenuItem value="4">4</MenuItem>
                            <MenuItem value="5">5</MenuItem>
                            <MenuItem value="6">6</MenuItem>
                            <MenuItem value="7">7</MenuItem>
                            <MenuItem value="8">8</MenuItem>
                        </TextField>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => { this.closeMarkerPropertiesDialog() }} color="primary">
                            Cancel
                        </Button>
                        <Button onClick={() => { this.saveMarkerProperties() }} color="primary">
                            Save
                        </Button>
                    </DialogActions>
                </Dialog>
                <Grid container>
                    <Grid item xs={3}>
                        <Accordion>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                            >
                                <Typography>{this.state.markersHeader}</Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <List style={{ width: '100%' }} disablePadding={true}>
                                    {this.state.markers.map((marker, i) =>
                                    (<ListItem key={marker.element} button onClick={() => this.handleItemOnClick(marker, i)}>
                                        <ListItemIcon>
                                            {marker.isVisible ? (<VisibilityIcon />) : (<VisibilityOffIcon />)}
                                        </ListItemIcon>
                                        <ListItemText classes={{ primary: marker.isVisible ? 'green' : 'red' }} primary={`${marker.name}`} />
                                    </ListItem>)
                                    )}
                                </List>
                            </AccordionDetails>
                        </Accordion>
                    </Grid>
                    <Grid item xs={9}>
                        <ToggleButton value="orbit" aria-label="Orbit" selected={this.state.isOrbitActive} onChange={() => {
                            this.setState({ isOrbitActive: !this.state.isOrbitActive });
                        }} >
                            Route
                        </ToggleButton>
                        <ToggleButton value="borders" aria-label="Labels" selected={this.state.isBordersActive} onChange={() => {
                            this.setState({ isBordersActive: !this.state.isBordersActive });
                        }} >
                            Borders
                        </ToggleButton>
                        <ToggleButton value="weather" aria-label="Weather" selected={this.state.isWeatherActive} onChange={() => {
                            this.setState({ isWeatherActive: !this.state.isWeatherActive });
                        }} >
                            Weather
                        </ToggleButton>
                        <ToggleButton value="wind" aria-label="Wind" selected={this.state.isWindActive} onChange={() => {
                            this.setState({ isWindActive: !this.state.isWindActive });
                        }} >
                            Wind
                        </ToggleButton>
                        <ToggleButton value="terrain" aria-label="Terrain" selected={this.state.isTerrainActive} onChange={() => {
                            this.setState({ isTerrainActive: !this.state.isTerrainActive });
                        }} >
                            Terrain
                        </ToggleButton>
                        <ToggleButton value="labels" aria-label="Labels" selected={this.state.areLabelsActive} onChange={() => {
                            this.setState({ areLabelsActive: !this.state.areLabelsActive });
                        }} >
                            Labels
                        </ToggleButton>
                        <Button onClick={() => { this.resetRoute()}}>
                            Reset Route
                        </Button>
                    </Grid>
                </Grid>
                <DynamicLayer isOrbitActive={this.state.isOrbitActive} showLabels={this.state.areLabelsActive} loopIndex={this.state.loopIndex} markers={this.state.markers} />
                {this.state.isWeatherActive ? <WeatherLayer showLabels={this.state.areLabelsActive} index={this.state.loopIndex} /> : undefined}
                {this.state.isBordersActive ? <BordersLayer showLabels={this.state.areLabelsActive} /> : undefined}
                {this.state.isWindActive ? <WindLayer showLabels={this.state.areLabelsActive} /> : undefined}
                {this.state.isTerrainActive ? <Terrain parent="#terrain-layer" y={-5} /> : undefined}
            </div>
        );
    }
}